@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@600&display=swap);
body{background-color:#343a40;text-align:center;margin:auto;width:50%}#drum-machine{font-family:"Ubuntu", sans-serif;display:inline-block}#drum-machine h1{text-transform:uppercase;margin:0;padding:15px 0px;color:#fff}#drum-machine #display{background-color:#dc3545;display:block;color:#fff;border:2px solid #007bff;text-transform:capitalize;font-family:"Source Sans Pro", sans-serif;font-size:25px;padding:15px;margin-bottom:5px}#drum-machine #controls{display:grid;grid-template-columns:auto auto;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;padding:20px;background-color:#dc3545;border:2px solid #007bff;margin-bottom:5px}#drum-machine #controls .powerArea .control{padding:6px 12px;cursor:pointer;border-radius:5px;background-color:#6699ff;font-size:20px;color:#fff;font-family:"Source Sans Pro", sans-serif}#drum-machine #controls .powerArea .control:active{color:#000;background-color:#fff}#drum-machine #controls .slider #volSlider{width:100%;margin:auto;margin-top:10px;background-color:#6699ff;outline:none}#drum-machine #controls .slider:focus{background:#6699ff}#drum-machine #drum-pad{display:inline-grid;grid-template-columns:auto auto auto;grid-template-rows:auto auto auto;border:2px solid #007bff;border-style:solid}#drum-machine #drum-pad .drum-pad{padding:25px 50px;background-color:#dc3545;color:#fff;border:2px solid #007bff;border-style:solid}#drum-machine #drum-pad .drum-pad:hover{cursor:pointer}@media (max-width: 575px){body{width:100%;height:auto}body #drum-machine{width:90%}body #drum-machine #drum-pad{display:flex}body #drum-machine #drum-pad .drum-pad{padding:0px;width:100%}body #drum-machine #drum-pad .drum-pad h2{margin:10px 5px}}

